@import '../../../assets/mixins/mixins.scss';

.QuestionnaireCardNoHome {
    display: flex;
    flex-direction: column;

    & .QuestionnaireTitle {
        margin: auto auto 0;
    }

    & .CTA {
        height: 30px;
        width: 90px;
        padding: unset;
        margin: 0 auto auto;
        
        &__Text {
            font-size: 12px;
        }
    }
}

@include tablet-p {
    .QuestionnaireCardNoHome {
        display: flex;

        & .QuestionnaireTitle {
            font-size: 16px;
            margin: auto;
            height: fit-content;
        }
    }
}

@include desktop {
    .QuestionnaireCardNoHome {
        width: 34.16vw;
        height:39.5vh;
        margin: auto;
        padding: 0 40px;

        & .QuestionnaireTitle {
            margin: auto auto 0;
            font-size: 3vh;
        }

        & .CTA {
            margin: 2vh auto auto;
            height: 60px;
            width: 120px;
            
            &__Text {
                font-size: 2vh;
            }
        }
    }
}
