@import '../../../assets/mixins/mixins.scss';

.QuestionnaireCardContact {
    display: flex;
    flex-direction: column;

    & > .InputBox {
        margin: 5px auto 0;
        height: 20px;
        width: 180px;
        font-size: 12px;
        padding: 5px;

        &::placeholder {
            font-size: 12px;
        }
    }

    & .CTA {
        margin:  5px auto auto;
        padding: 0;
        height: 15px;
        width: 60px;

        &__Text {
            font-size: 8px;
        }
    }
    
}

@include desktop {
    .QuestionnaireCardContact {
        width: 34.16vw;
        height:39.5vh;
        margin: auto;
        padding-top: 1vh;

        & .InputBox {
            margin: 2vh auto 1vh;
            width: 15vw;
            height: 5vh;
            font-size: 3vh;
            z-index: 99;

            &::placeholder {
                font-size: 3vh;
            }

            &:last-of-type{
                margin-top: 1vh;
                margin-bottom: 3vh;
            }
        }

        & .CTA {
            margin: 0 auto auto;
            width: 120px;
            height: 50px;

            &__Text {
                font-size: 2.5vh;
            }
        }
    }
}