@import '../../../assets/mixins/mixins.scss';


.QuestionnaireCardHome {
    width: fit-content;
    height: 80%;
    display: flex;
    flex-direction: column;
    margin: auto;

    &__Options {
        display: flex;
        justify-content: space-around;
    }
}

@include tablet-p {
    .QuestionnaireCardHome {
        padding: 20px 40px;
    }
}

@include desktop {
    .QuestionnaireCardHome {
        height: 26.34vh;
        width: 26.47vw;
        padding: 20px 0;

        &__Options {
            width: 7.6vw;
            margin: 2.5vh auto auto;
        }
    }
}