@import '../../assets/mixins/mixins.scss';

.GlyphAndText {
    &__Img {
        width: 20px;
    }

    &__Text {
        font-size: 12px;
    }

}

@include tablet-p {
    .GlyphAndText {
        &__Img {
            width: 2.34vw;
        }
    }
}

@include desktop {
    .GlyphAndText {

        & :hover {
            cursor: pointer;
        }
        
        &__Img {
            width: 2.34vw;
            height: 2.34vw;
        }

        &__Text {
            font-size: 1.9vh;
            font-weight: normal;
            text-transform: capitalize;
        }
    }
}