@import '../../assets/mixins/mixins.scss';

.Cards {
    height: calc(80px);
    position: relative;

    &__Container {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        position: absolute;
        top: -282px;
    }
}

@include tablet-p {
    .Cards {
        background-color: rgba(177, 177, 177, 0);
        position: relative;
    
        &__Container {
            margin-top: 0px;
            width: 100%;
            display: flex;
            flex-direction: row;
            justify-content: space-evenly;
            position: absolute;
            top: 0px;
            padding: 0 15px;
        }
    }
}
//1888 = 1540 + 348
@include desktop {
    .Cards {
        height: 20vh;
        display: flex;

        &__Container {
            position: relative;
            width: fit-content;
            top: -14vh;
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr;
            gap: 2vw;
            margin: 0 auto;
            justify-content: space-between;
        }
    }
}