@import '../../../assets/mixins/mixins.scss';

.QuestionnaireCardZipCode {
    width: fit-content;
    height: 80%;
    display: flex;
    flex-direction: column;
    margin: auto;
    margin-top: 10px;

    & .InputBox {
        margin: auto;
        margin-top: 10px;
        font-size: 14px;
        text-align: center;

        &::placeholder {
            font-size: 14px;
        }
    }

    & .CTA {
        margin-top: 10px;
        height: 20px;
        padding: 0;
        width: 50px;

        &__Text {
            font-size: 14px;
        }
    }
}

@include tablet-p {
    .QuestionnaireCardZipCode {



        & .CTA {
            margin: auto;
            padding: unset;

            &__Text {
                font-size: 15px;
            }
        }
    }
}

@include desktop {
    .QuestionnaireCardZipCode {
        height: 26.34vh;
        width: 26.47vw;
        display: flex;
        flex-direction: column;

        & .QuestionnaireTitle {
            margin: auto auto 0;
        }

        & .InputBox {
            margin: 1vh auto ;
        }

        & .CTA {
            margin: 1vh auto auto;
            
            &__Text {
                font-size: 2.5vh;
            }
        }
    }
}