@import '../../../assets/mixins/mixins.scss';


.QuestionnaireTitle {
    margin: auto;
    padding: 20px;
}

@include tablet-p {
    .QuestionnaireFinish {
        display: flex;

        & .QuestionnaireTitle {
            font-size: 16px;
            margin: auto;
            height: fit-content;
        }
    }
}

@include desktop {
    .QuestionnaireFinish {
        width: 34.16vw;
        height:39.5vh;
        margin: auto;
        padding: 0 40px;
    }
}
