@import '../../../assets/mixins/mixins.scss';

.QuestionnaireCardBill {
    display: flex;
    flex-direction: column;
    height: 80%;

    &__Input[type=range] {
        margin: 0 auto 50px;
        width: 80%;
        height: 0;
        -webkit-appearance: none;
        background-color: unset;
        
        &::-webkit-slider-runnable-track {
            background-color: #707070;
            height: 4px;
            width: 10px;
            border-radius: 2px;

            &:hover {
                cursor: pointer;
            }
        }

        &::-webkit-slider-thumb {
            box-shadow: 0px 0px 0px #000000;
            border: 0px solid #000000;
            height: 10px;
            width: 25px;
            border-radius: 7px;
            background: #464646;
            cursor: pointer;
            -webkit-appearance: none;
            margin-top: -3.5px;

            &:hover {
                cursor: pointer;
            }
        }

        &:focus {
            outline: none;
        }
    }

    & datalist {
        display: flex;
        height: 0px;
        justify-content: space-between;
        width: 90%;
        user-select: none;
        margin: -20px auto 0;
    }

    & .CTA {
        padding: 0;
        width: 60px;
        height: 30px;

        &__Text {
            font-size: 14px;
        }
    }
}

@include tablet-p {
    .QuestionnaireCardBill {

        & .CTA {
            width: 60px;
            height: 20px;
            padding: 0;
            margin: 0 auto;

            &__Text {
                font-size: 16px;
            }
        }
    }
}

@include desktop {

    .QuestionnaireCardBill {
        width: 34.16vw;
        height:39.5vh;
        margin: auto;
        padding-top: 3vh;

        &__Counter {
            margin-top: 2vh;
            font-size: 4vh;
        }

        &__Input[type=range] {
            margin-top: 1vh;
            margin-bottom: 0.5vh;

            &::-webkit-slider-runnable-track {
                background-color: #707070;
                height: 10px;
                width: 10px;
                border-radius: 2px;
    
                &:hover {
                    cursor: pointer;
                }
            }
    
            &::-webkit-slider-thumb {
                box-shadow: 0px 0px 0px #000000;
                border: 0px solid #000000;
                height: 15px;
                width: 25px;
                border-radius: 7px;
                background: #464646;
                cursor: pointer;
                -webkit-appearance: none;
                margin-top: -3.5px;
    
                &:hover {
                    cursor: pointer;
                }
            }
        }

        & datalist {
            margin-top: 20px;
        }

        & .CTA {
            padding: 10px 0;
            width: 120px;
            height: fit-content;
            margin: 0 auto auto;

            &__Text {
                font-size: 2.5vh;
            }
        }
    }
}