@import '../../assets/mixins/mixins.scss';

.InputBox {
    height: 25px;
    width: 70px;
    border-radius: 10px;
    border-color: #707070;
    font-family: Arial, Helvetica, sans-serif;
    outline: none;

    &::placeholder {
        font-size: 15px;
        font-family: Arial, Helvetica, sans-serif;
        color: rgba(36, 43, 58, 0.35);
        margin: auto 0;
    }
}

@include tablet-p {
    .InputBox {
        height: 30px;
        width: 80px;
        border-radius: 10px;
        border-color: #707070;
        align-self: center;
        text-align: center;
        justify-self: center;
        display: inline-flex;
        font-family: Arial, Helvetica, sans-serif;
    
        &::placeholder {
            font-size: 12px;
            font-family: Arial, Helvetica, sans-serif;
            color: rgba(36, 43, 58, 0.35);
            margin: auto 0;
        }
    }
}

@include desktop {
    .InputBox {
        height: 27px;
        width: 119px;
        border-radius: 10px;
        border-color: #707070;
        align-self: center;
        text-align: center;
        justify-self: center;
        display: inline-flex;
        font-family: Arial, Helvetica, sans-serif;
    
        &::placeholder {
            font-size: 10px;
            font-family: Arial, Helvetica, sans-serif;
            color: rgba(36, 43, 58, 0.35);
            margin: auto 0;
        }
    }

    .ZipCode {
        width: 15vw;
        height: 5vh;
        margin-top: 1.5vh;
        padding: unset;
        justify-items: center;
        font-size: 3vh;

        &::placeholder {
            font-size: 3vh;
            vertical-align: middle;
        }
    }
}