@import '../../assets/mixins/mixins.scss';//1.632

.Questionnaire {
    box-shadow: -20px 15px rgba(0, 0, 0, 0.16);
    background-image: url('../../assets/imgs/solar.jpeg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    display: flex;
    position: relative;
    width: 303px;
    height: 161px;
    margin: 22px auto 0;

    //width: 1.67
    //height: 2.5
    &__Container {
        margin: auto;
        color: #FFF;
        font-family: Arial, Helvetica, sans-serif;
        font-weight: bold;
        text-align: center;
        backdrop-filter: blur(15px) brightness(105%);
        width: 261px;
        height: 114.4px;
        border-radius: 10px;
    }
}
//2.8
@include tablet-p {
    .Questionnaire {
        position: absolute;
        top: 84px;
        right: 0;
        width: 365px;
        height: 220px;
        box-shadow: -20px 15px rgba(0, 0, 0, 0.16);
        // display: flex;
    
        &__Container {
            width: 291px;
            height: 144.4px;
            margin: auto;
        }
    }
}

@include desktop {
    .Questionnaire {
        position: absolute;
        top: 9vh;
        right: 0;
        width: 42.7vw;
        min-width: 443px;
        height: 49.7vh;
        min-height: 290px;
        box-shadow: -20px 15px rgba(0, 0, 0, 0.16);
        // display: flex;
    
        //w: 62 h:53
        &__Container {
            width: fit-content;
            height: fit-content;
            margin: auto;
            display: inline-flex;
        }
    }
}