@import '../../assets/mixins/mixins.scss';

.QuestionnaireTitle {
    text-align: center;
    color: white;
    font-size: 16px;
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;
    margin: auto ;
}

@include tablet-p {

    .QuestionnaireTitle {
        font-size: 24px;
    }
}

@include desktop {
    .QuestionnaireTitle {
        margin-bottom: 5px;
        font-size: 3.7vh;
        padding: unset;
    }
}